.nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
    background-color: #ffffff;
    padding: 2px;
}
.nav img{
    width: 20%;
}
.nav img:hover{
    width: 22%;
}
.div{
    background-color: #C22D2D;
    padding: 4px;
}
nav a{
    text-decoration: none;
    padding: 5px;
    margin-left: 15px;
    color: #fff;
    font-size: 1.3rem;
}
nav a:hover{
    color: #C22D2D;
    background-color: #fff;
    margin: 1px solid #C22D2D;
}
