.ma h2{
    font-size: 2.3rem;
}
.ma h3{
    font-size: 1.8rem;
}
.ma p{
    font-size: 1.1rem;
    padding: 4px 2.5rem;
}
.ima{
    display: flex;
    justify-content: space-around;
}

.ima img{
    width:80%;
    background-size: contain;
}
.im{
     display: flex;
    justify-content: space-around;
}
.im img{
    width:85%;
    background-size: contain;
}
.nav{
    display: flex;
    flex-direction: row;
    justify-content: flex-end
}
.nav a{
    text-decoration: none;
    color: #C22D2D;
    padding: .2rem .7rem;
    margin-top: .6rem ;
    margin-left: 1rem;
    border-radius: 3px;
    border: 1px solid #C22D2D;
    font-size: 1.2rem;
}
.nav a:hover{
    background-color: #C22D2D;
    color: #fff;
}