.ini{

}
.ini video{
    width: 85%;
}
hr{
    margin-bottom: 5rem;
}
.cards{

}
.cards h2{
    font-size: 2.5rem;
    font-weight: 400;
    color: rgb(70, 56, 56);
}
.card{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
    border-radius: 10px;
    border: 1px solid #C22D2D;
}
.card:hover{
    background-color: #c22d2d2a;
    padding: 2rem;
}
.img{
    display: inline-block;
    margin-top: 5%;
    width: 30%;
    padding: 1.5rem;
    margin: 1%;
}
.img:hover{
    width: 35%;
    padding: 0px;
    margin: 0px;
}
.img img{
    width: 100%;
}