@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2? family= Playfair+Display:ital,wght@1,400;1,500 & display=swap');
@import url('https://fonts.googleapis.com/css2? family= Lora:ital@1 & display=swap');

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
:root{
    color: #F2F2F2;
    color: #989DA6;
    color: #A68A72;
    color: #593E2E;
    color: #C22D2D;


font-family: 'Dancing Script', cursiva;
font-family: 'Ysabeau', sans-serif;
}
html, body, #root{
    text-align: center;
    background-color: #F2F2F2;
}
h1{
    color: #C22D2D;
    font-family: 'Ysabeau', sans-serif;
    padding: 10px;
    margin: 5px 0px;
}
h2{
    color: rgb(70, 56, 56);
    font-weight: 300;
    font-family: 'Ysabeau', sans-serif;
    padding: 8px;
    margin: 4px 0px;
}
h3{
    color: #C22D2D;
    font-family: 'Ysabeau', sans-serif;
    padding: 6px;
    margin: 3px 0px;
}h4{
    color: #333333;
    font-family: 'Ysabeau', sans-serif;
    padding: 6px;
    margin: 3px 0px;
}
p{
    color: #222222;
    font-family: 'Ysabeau', sans-serif;
    padding: 4px;
    margin: 2px 0px;
    font-family: 'Lora', serifa;
    text-align: center;
}
