.a{
    text-decoration: none;
    color: #C22D2D;
    font-size: 1.3rem;
    border-radius: 4px;
    border: 2px solid brown;
    padding: 4px;
    cursor: pointer;
    transition: .5s;
}
.a:hover{
    color: rgb(255, 255, 255);
    font-size: 1.4rem;
    border-radius: 4px;
    border: 2px solid rgb(163, 124, 124);
    padding: 6px;
    background-color: #C22D2D;
}